h1{
    font-family: 'Audiowide', Verdana, Tahoma, sans-serif;
    font-size: 40px;
    
}
.button, Button{
    font-family: 'Audiowide', Verdana, Tahoma, sans-serif;

}
span{
    font-family: 'Audiowide', Verdana, Tahoma, sans-serif;

}
h3, h4{
    font-family: 'Audiowide', Verdana, Tahoma, sans-serif;
}

.twitter {
    border-radius: 2px !important;
    background: #fff !important;
    border: 1px solid #ccc !important;
    outline: none !important;
    padding: 6px !important;
}

.twitter:hover,
.twitter:focus {
    border: 1px solid #56b4ef !important;
    -webkit-box-shadow: 0px 0px 3px 1px #c8def0 !important;
            box-shadow: 0px 0px 3px 1px #c8def0 !important;
}

.twitter-disabled {
    border-radius: 2px !important;
    background: #fff !important;
    border: 1px solid #ccc !important;
    outline: none !important;
    padding: 6px !important;
}

.custom-form {
    margin-top: 25px;
    -webkit-box-shadow: 0px 0px 3px 1px #e6e6e6;
            box-shadow: 0px 0px 3px 1px #e6e6e6;
    padding: 50px;
    padding-bottom: 25px;
    background-color: #eee;
    border-radius: 2px;
}

.custom-center {
    margin-top: 20px;
}

.category {
    width: 100%;
    margin-bottom: 5px;
}


