/* --------------------------- */
/* ----- answers container ----- */
/* --------------------------- */
#answers ul {
    list-style-type: none;
    padding: 0;
    margin-top: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

#answers li {
    background-color: #fff;
    border: 2px solid #2F409D;
    border-radius: 2px;
    min-height: 70px;
    width: 48%;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 30px;
    -webkit-transition: color .3s ease, border-color .3s ease, -webkit-transform .3s ease;
    transition: color .3s ease, border-color .3s ease, -webkit-transform .3s ease;
    -o-transition: color .3s ease, border-color .3s ease, transform .3s ease;
    transition: color .3s ease, border-color .3s ease, transform .3s ease;
    transition: color .3s ease, border-color .3s ease, transform .3s ease, -webkit-transform .3s ease;
    cursor: pointer;
}

#answers li span {
    background-color: #2F409D;
    color: #fff;
    font-size: 30px;
    -ms-flex: 75px 1;
        flex: 75px 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: background-color .3s ease;
    -o-transition: background-color .3s ease;
    transition: background-color .3s ease;
}

#answers li p {
    color: #0b0e21;
    font-size: 16px;
    font-weight: 600;
    -ms-flex: calc(100% - 75px) 1;
        flex: calc(100% - 75px) 1;
    margin: auto 20px;
    -webkit-transition: color .3s ease;
    -o-transition: color .3s ease;
    transition: color .3s ease;
}

#answers li:after { 
    display: block;
}

#answers li:hover {
    -webkit-transform: scale(1.03);
        -ms-transform: scale(1.03);
            transform: scale(1.03);
}

#answers li.right {
    border-color: #1ea55b;
    color: #1ea55b;
}

#answers li.right span {
    background-color: #1ea55b;
}

#answers li.right p {
    color: #1ea55b;
}

#answers li.wrong {
    border-color: #dc0a0a;
    color: #dc0a0a;
}

#answers li.wrong span {
    background-color: #dc0a0a;
}

#answers li.wrong p {
    color: #dc0a0a;
}



